<template>
        <transition name="fade">
            <div   v-if="infantsWarning.status" class="search-flights__count__tooltip">
                {{infantsWarning.text}}
            </div>
        </transition>
</template>
<script>
export default {
    data(){
        return{
            infantsWarning :{
                status: false,
                text : 'Не более одного младенеца (без места) на одного взрослого. Если младенцев больше, добавьте еще пассажиров в категории «Дети до 12 лет».'
            }
        }
    },
    props:['infantsState'],
    methods:{
        tooltipON(){
            if(!this.infantsState){
                this.infantsWarning.status = true
            }
        },
        tooltipOFF(){
            this.infantsWarning.status = false
        }
    }
}
</script>
