<template>
        <li class="search-flights_passeng__item" >
                <div class="search-flights_passeng__wrapp">
                <strong class="search-flights_passeng__name">{{pass.title}}</strong><br>
                <small class="search-flights_passeng__info">{{pass.info}}</small>
                </div>
                <div class="search-flights__count">
                    <a  v-if="this.pass.countName === 'adults' "  @mouseover="tooltipON" @mouseout="tooltipOFF" class="btn__minus" 
                    v-bind:class="{'decrement': this.pass.state.decrement}" 
                    v-on:click="decrementCount">
                    <toolTip ref="toolTip" v-bind:infantsState="infantsState" />
                    </a>
                    <a  v-else class="btn__minus" 
                    v-bind:class="{'decrement': this.pass.state.decrement}" 
                    v-on:click="decrementCount">
                    </a>
                    <span >{{pass.count}}</span>
                    <a  v-if="this.pass.countName === 'infants' "   @mouseover="tooltipON" @mouseout="tooltipOFF" class="btn__plus" type="button"  @click="incrementCount"
                    v-bind:class="{'increment': this.pass.state.increment }">
                        <toolTip ref="toolTip" v-bind:infantsState="infantsState" />
                    </a>
                    
                    <a  v-else class="btn__plus" type="button"  @click="incrementCount"
                    v-bind:class="{'increment': this.pass.state.increment }">
                    </a>
                </div>
        </li>
</template>
<script>
import toolTip from './toolTip.vue'
export default {
    name: "searchFlightsItem",
    data(){
        return {
        } 
    },
    props:['pass','infantsState'],
    components: {
        toolTip:toolTip
        },
    methods: {
        tooltipON(){
                this.$refs.toolTip.tooltipON()
        },
        tooltipOFF(){
                this.$refs.toolTip.tooltipOFF()
        },
        decrementCount(){
            const numOfdec = this.countName === 'adults' ? 1 : 0;
            if(!this.pass.state.decrement){
                this.pass.count -= 1;
            }
            if(this.pass.count===numOfdec){
                this.pass.state.decrement =true;
            }
            this.$emit('decrementCount')
        },
        incrementCount(){
            if(!this.pass.state.increment){
                this.pass.count += 1;
                this.pass.state.decrement =false;
            }
                this.$emit('incrementCount')
            
        }
    },

}
</script>
<style scoped>
