    display: flex;
    align-items: center;
    color: #ffffff;
}
.search-flights__count span {
    margin: 5px;
    font-size: 17px;
    font-weight: 700;
}
.search-flights__count a{
    position: relative;
    display: block;
    width: 30px;
    height:  30px;
    padding: 0;
    background-color: #2196f3;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    background-size: 15px;
    background-position: center center; 
    background-repeat: no-repeat;
    outline: none;
}
.search-flights__count button:focus{
    box-shadow: 0px 0px 6px 1px #2196F2;
}
.btn__minus{
    background-image: url(../assets/minus_icon.svg)
}
.btn__plus{
        background-image: url(../assets/plus_icon.svg)
}
.search-flights__count .decrement ,.search-flights__count .increment{
    background-color: #586E7E;
}

</style>



