import Vue from 'vue';
import searchFlightsItem from './search-flights-item.vue'
import { fstat } from 'fs';
export default {
    name:'searchFlights',
    data(){
        return{
            origin:'',//пункта отправления IATA код указывается буквами верхнего регистра, 
            destination:'',//пункта назначения. IATA код указывается буквами верхнего регистра, 
            trip_class: "Y",// класс перелета (Y — эконом, C — бизнес);
            passengers: [
