                    info:'Старше 12 лет',
                    countName :'adults',
                    count:1,
                    state:{
                        decrement:true,
                        increment:false
                    }

                },
                {   
                    id:2,
                    title: 'Дети',
                    info:'От 2 до 12 лет',
                    countName :'children',
                    count:0,
                    state:{
                        decrement:true,
                        increment:false
                    }

                },
                {
                    id:3,
                    title: 'Младенцы',
                    info:'До 2 лет',
                    countName :'infants',
                    count:0,
                    state:{
                        decrement:true,
                        increment:false
                    }
                
                }
            ], 
            infantsState: true ,

            link: "/listOfFlights",
        }
    },
    components: {
        searchFlightsItem:searchFlightsItem
        },
    methods:{
        swapPlaces(event){
                event.preventDefault()
                const saveValue = this.origin
                this.origin  = this.destination
                this.destination = saveValue
        },
        submitForm (){

        const formData = new FormData();
        formData.set('sending', this.sending)
        formData.set('arrival', this.arrival)
        fetch('./back.php',{
            method: 'POST',
            mode: 'no-cors',
            body: formData,
            })
        .then(response =>{
        if(response.status >= 200 && response.status < 300){ 
                console.log(request.response)
        }else{

        
    }
    })
    },
    validAmountCheck(){
        const sum  = this.passengers.reduce((acc,item)=>{
            acc+=item.count;
                return acc
        },0)
        const MAX_SUM = 9;
        if(sum >=  MAX_SUM) {
            this.passengers.forEach(item=>{
                item.state.increment = true 
            })
        }
        if( MAX_SUM > sum) {
            this.passengers.forEach(item=>{
                item.state.increment = false 
            })
        
        }
    },
    babyValidCange(){
        const result = this.passengers[0].count === this.passengers[2].count
        if(result){
            if(!this.passengers[2].state.increment){
                this.passengers[2].state.increment = true
            }
            if(!this.passengers[0].state.decrement){
                this.passengers[0].state.decrement = true
            }
            this.infantsState =false
        }else{
            if(this.passengers[0].count>this.passengers[2].count){
                this.passengers[0].state.decrement = false
                this.infantsState = true
            
            }
        }
        
    }
}

}
